import { onMounted, nextTick, ref } from 'vue';

/*    { */
const getFooterHeight = () => {
  //  : 
  const footerHeight = ref(0);

  //  :  
  const updateFooterHeight = () => {
    footerHeight.value = document.querySelector('#footer').offsetHeight;
  };

  //    ,
  onMounted(async () => {
    try {
      // #footer  
      await nextTick(() => {
        updateFooterHeight();
      });
    } catch (err) {
      console.log(err);
    }
  });

  return {
    footerHeight,
  };
};
/* }     */

export default getFooterHeight;
