import { onMounted, nextTick, ref } from 'vue';

/*    { */
const getHeaderHeight = () => {
  //  
  const headerHeight = ref(0);

  //  :  
  const updateHeaderHeight = () => {
    headerHeight.value = document.querySelector('#header').offsetHeight;
  };

  //    ,
  onMounted(async () => {
    // #header  
    try {
      await nextTick(() => {
        updateHeaderHeight();
      });
    } catch (err) {
      console.log(err);
    }
  });

  return {
    headerHeight,
  };
};
/* }     */

export default getHeaderHeight;
